<template>
  <div class="hs-container">
    <c-header :title="$t('base.ChangePassword')" isBackNav></c-header>
    <hs-section>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <input
            maxlength='50'
            v-model="oldPassword"
            type="password"
            :placeholder="$t('base.PleaseEnterTheOldPassword')"/>
        </div>
      </hs-cell-1>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <input
            maxlength='50'
            v-model="newPassword"
            type="password"
            :placeholder="$t('base.PleaseEnterANewPassword')"/>
        </div>
      </hs-cell-1>
      <hs-cell-1 :showLogo="false">
        <div class="custom-cell">
          <input
            maxlength='50'
            v-model="newPasswordAgain"
            type="password"
            :placeholder="$t('base.PleaseEnterYourNewPasswordAgain')"/>
        </div>
      </hs-cell-1>
    </hs-section>
    <hs-gradient-button style="margin-top: 30px" :title="$t('base.Confirm')" @click="_openSubmitDialog"></hs-gradient-button>
  </div>
</template>
<script type="text/ecmascript-6">
// 重置密码
import CHeader from 'components/c-header/c-header'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import {hex_sha1} from 'common/js/sha1' // eslint-disable-line
import {kpiUserModifyPassword} from 'api'
export default {
  components: {
    CHeader,
    HsSection,
    HsGradientButton,
    HsCell1
  },
  data() {
    return {
      oldPassword: '',
      newPassword: '',
      newPasswordAgain: ''
    }
  },
  methods: {
    _openSubmitDialog() {
      if (this.noValue(this.oldPassword)) {
        this.$toast(this.$t('TM.PleaseEnterTheOldPassword'))
        return
      } else if (this.noValue(this.newPassword)) {
        this.$toast(this.$t('TM.PleaseEnterANewPassword'))
        return
      } else if (this.noValue(this.newPasswordAgain)) {
        this.$toast(this.$t('TM.PleaseEnterYourNewPasswordAgain'))
        return
      } else if (this.newPassword === this.oldPassword) {
        this.$toast(this.$t('TM.OldAndNewPasswordsMustNotBeTheSame'))
        return
      } else if (this.newPassword !== this.newPasswordAgain) {
        this.$toast(this.$t('TM.InconsistentPasswordEnteredTwice'))
        return
      } else if (this.newPassword.length < 6) {
        this.$toast(this.$t('TM.PasswordMustBeNoLessThanSixDigitsInLength'))
        return
      }
      this.dialog = this.$createDialog({
        type: 'confirm',
        icon: 'cubeic-alert',
        title: this.$t('base.Tips'),
        content: this.$t('TM.WhetherToConfirmThePasswordChange'),
        confirmBtn: {
          text: this.$t('base.Confirm'),
          active: true,
          disabled: false,
          href: 'javascript:;'
        },
        cancelBtn: {
          text: this.$t('base.Cancel'),
          active: false,
          disabled: false,
          href: 'javascript:;'
        },
        onConfirm: () => {
          this._submitPassword()
        }
      })
      this.dialog.show()
    },
    _submitPassword() {
      let params = {
        OldPassword: hex_sha1(this.oldPassword),
        NewPassword: hex_sha1(this.newPassword)
      }
      kpiUserModifyPassword(params).then((res) => {
        console.log('修改密码：', res)
        if (res.ResultData === true) {
          this.$toast(this.$t('TM.ChangeSucceed'))
          this.$router.go(-1)
        } else {
          this.$toast(this.$t('TM.ChangeFailed'))
        }
      })
    }
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  @extend .flex-column
  @extend .flex-start
  width 100%
  .custom-cell
    width 100%
    height 44px
    >input
      @extend .flex-row
      @extend .flex-start
      width 100%
      height 100%
</style>



